import { Component } from '@angular/core';
import * as AdsJson from "../assets/ads.json";

interface AD {
  id: number;
  propertyType: string;
  address: string;
  landmark: string;
  contact: number;
  cents: number;
  cost: number;
  sft: number;
  costPerSft: number;
  totalCost: number;
  acers: number;
  costPerAcer: number;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';

  ads: AD[];

  constructor(){
    let vv: any = AdsJson;
    let jj = JSON.stringify(vv);
    this.ads = JSON.parse(jj);
    //console.log(jj);
  }
}



